@import '../core/index-noreset.scss';
@import './scss/variable.scss';

#{$star-prefix}-wrapper {
  cursor: pointer;
  display: inline-block;
  &:focus {
    outline: none;
  }
  .#{$css-prefix}star {
    color: $gray-06;
    transition: all .3s;
    &:hover {
      color: $primary-warning;
    }
    &.#{$css-prefix}starred {
      color: $primary-warning;
      &:hover {
        color: $hover-warning;
      }
    }
  }
}
