@charset "UTF-8";
@import './mixin.scss';
@import './variable.scss';

// tag placeholders
// --------------------------------------------------

%ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}

// 标签的 5 种形态：纯文字、灰底、品牌色底、纯边框、品牌色边框
%tag-statement-text {
  @include tag-statement(
    $tag-normal-text-color,
    transparent,
    transparent,
    $tag-normal-text-color-hover
  );
}

