@import '../../icon/size.scss';
/// @group form-element
/// @module form-element: 表单通用
/// @type String
/// @tag form-element
/// @category utility
/// @family form-element
/// @varPrefix $form-element-
/// @classPrefix {prefix}-form-element
////

/// height
/// @semantic 小小号组件高度
$form-element-xs-height: $S28 - (14px - $font-size-body)*2 !default;

/// height
/// @semantic 小号组件高度
$form-element-small-height: $S32 - (14px - $font-size-body)*2 !default;

/// height
/// @semantic 中号组件高度
$form-element-medium-height: $S36 - (14px - $font-size-body)*2 !default;

/// height
/// @semantic 大号组件高度
$form-element-large-height: $S40 - (14px - $font-size-body)*2 !default;

/// text
/// @semantic 小小号组件字体
$form-element-xs-font-size: $font-size-body !default;
/// text
/// @semantic 小号组件字体
$form-element-small-font-size: $font-size-body !default;

/// text
/// @semantic 中号组件字体
$form-element-medium-font-size: $font-size-body !default;

/// text
/// @semantic 大号组件字体
$form-element-large-font-size: $font-size-body + 2px !default;

/// icon
/// @semantic 小小号组件 icon
$form-element-xs-icon-size: $icon-xs !default;

/// icon
/// @semantic 小号组件 icon
$form-element-small-icon-size: $icon-xs !default;

/// icon
/// @semantic 中号组件 icon
$form-element-medium-icon-size: $icon-s !default;

/// icon
/// @semantic 大号组件 icon
$form-element-large-icon-size: $icon-l !default;

/// corner
/// @semantic 小小号组件圆角
$form-element-xs-corner: $shape-radius !default;
/// corner
/// @semantic 小号组件圆角
$form-element-small-corner: $shape-radius !default;

/// corner
/// @semantic 中号组件圆角
$form-element-medium-corner: $shape-radius !default;

/// corner
/// @semantic 大号组件圆角
$form-element-large-corner: $shape-radius !default;
