// ------------------- 图标尺寸 ---------------------- //
/// icon-xs
$icon-xs: $S12 !default;
/// icon-s
$icon-s: $S16 - (14px - $font-size-body)*2 !default;

/// icon-m
$icon-m: $S20 - (14px - $font-size-body)*2 !default;

/// icon-l
$icon-l: $S24 - (14px - $font-size-body)*2 !default;

/// icon-xl
$icon-xl: $S32 - (14px - $font-size-body)*4 !default;

/// icon-xxl
$icon-xxl: $S40 - (14px - $font-size-body)*4 !default;

/// icon-xxxl
$icon-xxxl: $S48 - (14px - $font-size-body)*4 !default;
