@keyframes cellZoomIn {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes cellHover {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes enterToLeft {
  0% {
    transform: translate(-40%);
    opacity: 0;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
    transform: translate(0%);
  }
}

@keyframes enterToRight {
  0% {
    transform: translate(40%);
    opacity: 0;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
    transform: translate(0%);
  }
}
